// downloadPdf.jsx - Generate Python Web Developer Roadmap PDF
import jsPDF from 'jspdf';

/**
 * Generate and download the Python Web Developer roadmap as a PDF
 * @param {Array} phases - Array of phase objects from roadmapData
 * @returns {Object} - Success status and filename or error
 */
export const downloadRoadmapPDF = async (phases) => {
  try {
    const pdf = new jsPDF({
      orientation: 'portrait',
      unit: 'mm',
      format: 'a4'
    });

    const pageWidth = pdf.internal.pageSize.getWidth();
    const pageHeight = pdf.internal.pageSize.getHeight();
    const margin = 20;
    const contentWidth = pageWidth - (2 * margin);

    // Color palette matching the roadmap
    const colors = {
      phase0: [66, 153, 225],   // blue for Phase 0
      phase1: [72, 187, 120],   // green
      phase2: [66, 153, 225],   // blue
      phase3: [159, 122, 234],  // purple
      phase4: [245, 101, 101],  // red
      phase5: [237, 137, 54],   // orange
      phase6: [102, 126, 234],  // indigo
      phase7: [237, 100, 166],  // pink
      phase8: [56, 178, 172],   // teal
      phase9: [236, 201, 75],   // yellow
      phase10: [74, 85, 104],   // gray-800
      textDark: [26, 32, 44],
      textMedium: [74, 85, 104],
      textLight: [113, 128, 150],
      border: [203, 213, 224],
      background: [247, 250, 252]
    };

    const phaseColors = [
      colors.phase1,  // Phase 1
      colors.phase2,  // Phase 2
      colors.phase3,  // Phase 3
      colors.phase4,  // Phase 4
      colors.phase5,  // Phase 5
      colors.phase6,  // Phase 6
      colors.phase7,  // Phase 7
      colors.phase8,  // Phase 8
      colors.phase9,  // Phase 9
      colors.phase10  // Phase 10
    ];

    let yPos = margin;
    let currentPage = 1;

    // Helper function to check if we need a new page
    const checkNewPage = (requiredSpace) => {
      if (yPos + requiredSpace > pageHeight - margin) {
        pdf.addPage();
        yPos = margin;
        currentPage++;
        return true;
      }
      return false;
    };

    // Helper function to wrap text
    const wrapText = (text, maxWidth) => {
      return pdf.splitTextToSize(text, maxWidth);
    };

    // Cover Page
    // Title
    pdf.setFillColor(...colors.phase0);
    pdf.rect(0, 0, pageWidth, 80, 'F');

    pdf.setTextColor(255, 255, 255);
    pdf.setFontSize(32);
    pdf.setFont('helvetica', 'bold');
    pdf.text('Python Web Development', pageWidth / 2, 35, { align: 'center' });

    pdf.setFontSize(20);
    pdf.setFont('helvetica', 'normal');
    pdf.text('A Comprehensive Learning Roadmap', pageWidth / 2, 50, { align: 'center' });

    pdf.setFontSize(12);
    pdf.text('2025 Edition', pageWidth / 2, 65, { align: 'center' });

    yPos = 100;

    // Add introduction section
    pdf.setTextColor(...colors.textDark);
    pdf.setFontSize(14);
    pdf.setFont('helvetica', 'bold');
    pdf.text('Welcome to Your Python Web Development Journey', margin, yPos);

    yPos += 10;
    pdf.setFontSize(10);
    pdf.setFont('helvetica', 'normal');
    pdf.setTextColor(...colors.textMedium);

    const introText = 'This comprehensive roadmap will guide you from absolute beginner to industry-ready Python web developer. Each phase builds upon the previous one, ensuring you develop a solid foundation and practical skills that companies value.';
    const wrappedIntro = wrapText(introText, contentWidth);
    wrappedIntro.forEach(line => {
      pdf.text(line, margin, yPos);
      yPos += 6;
    });

    yPos += 10;
    pdf.setFont('helvetica', 'bold');
    pdf.text('How to Use This Roadmap:', margin, yPos);
    yPos += 7;

    pdf.setFont('helvetica', 'normal');
    const instructions = [
      '‚Ä¢ Follow each phase sequentially - they build upon each other',
      '‚Ä¢ Complete the practice projects - they solidify your understanding',
      '‚Ä¢ Take your time - depth matters more than speed',
      '‚Ä¢ Build a portfolio as you learn - document your projects on GitHub',
      '‚Ä¢ Join communities and engage with other learners'
    ];

    instructions.forEach(instruction => {
      pdf.text(instruction, margin + 5, yPos);
      yPos += 6;
    });

    // Start phases on new page
    pdf.addPage();
    yPos = margin;
    currentPage++;

    // Process each phase
    phases.forEach((phase, phaseIndex) => {
      checkNewPage(60); // Check if we need a new page for the phase header

      // Phase Header Box
      const phaseColor = phaseColors[phaseIndex % phaseColors.length];

      // Phase header background
      pdf.setFillColor(...phaseColor);
      pdf.roundedRect(margin, yPos, contentWidth, 25, 3, 3, 'F');

      // Phase title
      pdf.setTextColor(255, 255, 255);
      pdf.setFontSize(16);
      pdf.setFont('helvetica', 'bold');
      const phaseTitle = `PHASE ${phase.id}`;
      pdf.text(phaseTitle, margin + 5, yPos + 8);

      pdf.setFontSize(14);
      pdf.text(phase.title, margin + 5, yPos + 16);

      yPos += 30;

      // Subtitle and description
      pdf.setTextColor(...colors.textMedium);
      pdf.setFontSize(10);
      pdf.setFont('helvetica', 'italic');
      pdf.text(phase.subtitle, margin, yPos);

      yPos += 6;
      pdf.setFont('helvetica', 'normal');
      const wrappedDesc = wrapText(phase.description, contentWidth);
      wrappedDesc.forEach(line => {
        checkNewPage(8);
        pdf.text(line, margin, yPos);
        yPos += 5;
      });

      yPos += 8;

      // Process sections
      phase.sections.forEach((section, sectionIndex) => {
        checkNewPage(40); // Ensure space for section header and some items

        // Section box
        pdf.setDrawColor(...colors.border);
        pdf.setFillColor(255, 255, 255);

        // Calculate section height
        let sectionHeight = 12; // Header
        section.items.forEach(item => {
          const wrappedItem = wrapText(item, contentWidth - 20);
          sectionHeight += (wrappedItem.length * 5) + 2;
        });
        sectionHeight += 5; // Bottom padding

        // Check if section fits, otherwise add new page
        if (yPos + sectionHeight > pageHeight - margin) {
          pdf.addPage();
          yPos = margin;
          currentPage++;
        }

        const sectionStartY = yPos;

        // Section header
        pdf.setFillColor(...phaseColor);
        pdf.rect(margin, yPos, contentWidth, 10, 'F');

        pdf.setTextColor(255, 255, 255);
        pdf.setFontSize(11);
        pdf.setFont('helvetica', 'bold');
        pdf.text(section.title, margin + 3, yPos + 7);

        yPos += 12;

        // Section items
        pdf.setTextColor(...colors.textDark);
        pdf.setFontSize(9);
        pdf.setFont('helvetica', 'normal');

        section.items.forEach((item, itemIndex) => {
          const bullet = `${itemIndex + 1}.`;
          const itemText = item;

          // Check if this item will fit
          const wrappedItem = wrapText(itemText, contentWidth - 15);
          if (yPos + (wrappedItem.length * 5) > pageHeight - margin) {
            // Draw border for current section part
            pdf.setDrawColor(...colors.border);
            pdf.setLineWidth(0.5);
            pdf.rect(margin, sectionStartY, contentWidth, yPos - sectionStartY);

            pdf.addPage();
            yPos = margin;
            currentPage++;
          }

          pdf.setFont('helvetica', 'bold');
          pdf.text(bullet, margin + 3, yPos);

          pdf.setFont('helvetica', 'normal');
          wrappedItem.forEach((line, lineIndex) => {
            pdf.text(line, margin + 10, yPos);
            yPos += 5;
          });

          yPos += 1;
        });

        // Draw section border
        pdf.setDrawColor(...colors.border);
        pdf.setLineWidth(0.5);
        pdf.rect(margin, sectionStartY, contentWidth, yPos - sectionStartY);

        yPos += 8;
      });

      yPos += 5;
    });

    // Final Tips Page
    checkNewPage(100);

    // Final tips header
    pdf.setFillColor(...colors.phase0);
    pdf.roundedRect(margin, yPos, contentWidth, 20, 3, 3, 'F');

    pdf.setTextColor(255, 255, 255);
    pdf.setFontSize(14);
    pdf.setFont('helvetica', 'bold');
    pdf.text('üèÜ Final Tips to Become Industry-Ready', pageWidth / 2, yPos + 13, { align: 'center' });

    yPos += 28;

    pdf.setTextColor(...colors.textDark);
    pdf.setFontSize(11);
    pdf.setFont('helvetica', 'normal');
    const finalMessage = 'Congratulations! You\'ve completed the Python Web Development Mastery Roadmap and are ready to design scalable, robust web applications.';
    const wrappedFinal = wrapText(finalMessage, contentWidth);
    wrappedFinal.forEach(line => {
      pdf.text(line, margin, yPos);
      yPos += 6;
    });

    yPos += 10;

    pdf.setFont('helvetica', 'bold');
    pdf.text('What Makes You Industry-Ready:', margin, yPos);
    yPos += 8;

    pdf.setFont('helvetica', 'normal');
    const skills = [
      '‚úÖ Build secure, production-grade APIs with authentication and authorization',
      '‚úÖ Design efficient databases with proper indexing and query optimization',
      '‚úÖ Write comprehensive tests to ensure code quality and reliability',
      '‚úÖ Deploy applications to cloud platforms with CI/CD pipelines',
      '‚úÖ Scale applications to handle increasing user loads',
      '‚úÖ Debug and resolve production issues effectively',
      '‚úÖ Collaborate in teams using Git and code review processes',
      '‚úÖ Understand system architecture and design patterns'
    ];

    skills.forEach(skill => {
      checkNewPage(8);
      const wrappedSkill = wrapText(skill, contentWidth - 5);
      wrappedSkill.forEach(line => {
        pdf.text(line, margin + 3, yPos);
        yPos += 5;
      });
      yPos += 2;
    });

    yPos += 10;

    pdf.setFillColor(240, 255, 244);
    pdf.setDrawColor(...colors.phase1);
    pdf.setLineWidth(0.5);
    pdf.roundedRect(margin, yPos, contentWidth, 25, 2, 2, 'FD');

    pdf.setTextColor(...colors.textDark);
    pdf.setFont('helvetica', 'bold');
    pdf.setFontSize(10);
    pdf.text('üí° Remember:', margin + 5, yPos + 8);

    pdf.setFont('helvetica', 'normal');
    pdf.setFontSize(9);
    const reminder = 'This is what companies pay for. Focus on building real projects, contribute to open source, and continuously learn. The journey never truly ends in tech!';
    const wrappedReminder = wrapText(reminder, contentWidth - 10);
    let reminderY = yPos + 14;
    wrappedReminder.forEach(line => {
      pdf.text(line, margin + 5, reminderY);
      reminderY += 5;
    });

    // Footer on last page
    yPos = pageHeight - 20;
    pdf.setTextColor(...colors.textLight);
    pdf.setFontSize(8);
    pdf.setFont('helvetica', 'italic');
    pdf.text('Generated with Python Web Development Roadmap ‚Ä¢ 2025 Edition', pageWidth / 2, yPos, { align: 'center' });
    pdf.text('Keep Learning, Keep Building! üöÄ', pageWidth / 2, yPos + 5, { align: 'center' });

    // Add page numbers to all pages
    const totalPages = pdf.internal.getNumberOfPages();
    for (let i = 1; i <= totalPages; i++) {
      pdf.setPage(i);
      pdf.setTextColor(...colors.textLight);
      pdf.setFontSize(8);
      pdf.text(`Page ${i} of ${totalPages}`, pageWidth - margin, pageHeight - 10, { align: 'right' });
    }

    // Generate filename with timestamp
    const timestamp = new Date().toISOString().split('T')[0];
    const filename = `Python-WebDev-Roadmap-${timestamp}.pdf`;

    // Save the PDF
    pdf.save(filename);

    return {
      success: true,
      filename: filename
    };

  } catch (error) {
    console.error('Error generating PDF:', error);
    return {
      success: false,
      error: error.message
    };
  }
};

export default downloadRoadmapPDF;